.article {
  font-family: Roboto;
  // width: 309px;
  height: 254px;

  .content {
    // width: 309px;
    height: 216px;

    padding: 14px;

    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    border: 1px solid #f8f9fa;
    box-sizing: border-box;
    border-radius: 5px;
    background-color: rgba(41, 57, 77, 0.72);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 0;
      background: linear-gradient(
        0deg,
        rgba(41, 57, 77, 0.9) 0%,
        rgba(41, 57, 77, 0.72) 35.42%,
        rgba(85, 85, 85, 0.09) 67.71%,
        rgba(41, 57, 77, 0) 100%
      );
      border-radius: 5px;
    }
  }

  .category {
    height: 20px;

    position: relative;
    z-index: 1;

    padding: 0 14px;
    margin-bottom: auto;
    margin-left: auto;
    background: #7da0fb;
    border-radius: 2px;

    font-family: Roboto;
    font-size: 12px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.02em;
    white-space: nowrap;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;

    color: #ffffff;
    text-shadow: 0px 4px 4px rgba(41, 57, 77, 0.1);
  }

  .community {
    position: relative;
    z-index: 1;
    height: 20px;
    border-left: 3px solid #7da0fb;
    padding-left: 6px;
    margin-bottom: 7px;
    font-size: 14px;
    line-height: 20px;
    color: #f8f9fa;

    white-space: nowrap;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .title {
    position: relative;
    z-index: 1;
    color: #ffffff;
    text-shadow: 0px 4px 4px rgba(41, 57, 77, 0.1);
    margin-bottom: 20px;
    font-family: Roboto;
    font-weight: 500;
    font-size: 18px;
    line-height: 1.266;
    max-height: 2 * 18px * 1.266;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
}

.publishedAt {
  position: absolute;
  top: -20px;
  right: 0;
  font-family: Roboto;
  font-style: normal;
  font-weight: 300;
  font-size: 10px;
  line-height: 12px;
  text-align: right;

  color: #6d7f92;
}

.actionsContainer {
  position: relative;
  display: flex;
  z-index: 1;
}

.action {
  width: 20%;
  height: 24px;

  background: rgba(41, 57, 77, 0.3);
  border-radius: 5px;

  font-size: 10px;
  line-height: 12px;
  display: flex;
  align-items: center;
  justify-content: center;

  color: #f1f2f4;
  cursor: pointer;

  &:not(:last-child) {
    margin-right: 14px;
  }

  svg {
    opacity: 0.7;
  }
}

.activeAction {
  background: #29394d;
  border: 0.5px solid rgba(199, 232, 254, 0.1);
  color: #18a0fb;

  .actionCount,
  svg {
    opacity: 1;
  }
}

.actionCount {
  margin-left: 4px;
  opacity: 0.7;
}

.authorsContainer {
  margin-bottom: 4px;
}

.smallBH {
  .content {
    height: 318px;
  }
  .action {
    width: 45px;
  }
}
.medium {
  width: 100%;
  height: 100%;

  .content {
    width: 100%;
    height: 20rem;
  }
  .action {
    width: 45px;
  }
}
// Large article
.large {
  width: 100%;
  height: 100%;

  .content {
    width: 100%;
    height: 485px;
  }

  .community {
    font-size: 16px;
  }

  .title {
    font-size: 22px;
    max-height: 2 * 22px * 1.266;
    margin-bottom: 14px;
  }

  .summary {
    position: relative;
    font-weight: 300;
    font-size: 12px;
    line-height: 1.4;
    color: #f8f9fa;
    margin-bottom: 14px;
    max-height: 3 * 12px * 1.4;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }
  .action {
    width: 45px;
  }
}

// Template 2
.articleTemplate2,
.articleTemplate4 {
  .content {
    position: relative;
    height: 20rem;
    background-color: #ffffff;
    padding: 1rem;
  }

  .category {
    height: 20px;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;

    padding: 0 14px;
    margin-bottom: auto;
    margin-left: auto;
    background: #7da0fb;
    border-radius: 2px;

    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    color: #ffffff;
    text-shadow: 0px 4px 4px rgba(41, 57, 77, 0.1);
  }

  .community {
    position: relative;
    z-index: 1;
    height: 20px;
    border-left: 3px solid #7da0fb;
    color: #3c4e64;
    padding-left: 6px;
    margin-top: 10px;
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 20px;
    white-space: nowrap;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .title {
    color: #29394d;
    margin-bottom: 20px;
    font-family: Roboto;
    font-weight: 500;
    font-size: 18px;
    line-height: 1.266;
    max-height: 90px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
  }

  .summary {
    position: relative;
    height: 5rem;
    overflow-y: auto;
    font-weight: 300;
    font-size: 12px;
    line-height: 1.4;
    color: #3c4e64;
    margin-bottom: 14px;
    text-overflow: ellipsis;
    display: block;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }

  // .actionsContainer {
  //   position: absolute;
  //   bottom: 15px;
  //   left: 1rem;
  // }

  .action {
    background-color: #f8f9fa;
    color: #6d7f92;
  }

  .activeAction {
    background: #f3faff;
    border: 0.5px solid rgba(199, 232, 254, 0.5);
    color: #18a0fb;
  }
}
// End Template 2

